import { Reducer } from "redux";
import { BackgroundShape } from "../enums/background-shape";
import { SettingsActionType } from "../enums/settings";
import { SettingsAction, SettingsState } from "../types/state/settings";

export const defaultSettingsState: SettingsState = {
    backgroundShape: BackgroundShape.SPHERE,
    showSettingsModal: false,
    backgroundAutoRotate: true,
    sectionsFade: true,
};

export const settingsReducer: Reducer<SettingsState, SettingsAction> = (
    state: SettingsState = defaultSettingsState,
    action: SettingsAction,
) => {
    switch (action.type) {
        case SettingsActionType.SET_BACKGROUND_SHAPE:
            return {
                ...state,
                backgroundShape: action.payload as BackgroundShape,
            };
        case SettingsActionType.SET_SHOW_SETTINGS_MODAL:
            return {
                ...state,
                showSettingsModal: action.payload === "true",
            };
        case SettingsActionType.SET_BACKGROUND_AUTO_ROTATE:
            return {
                ...state,
                backgroundAutoRotate: action.payload === "true",
            };
        case SettingsActionType.SET_SECTIONS_FADE:
            return {
                ...state,
                sectionsFade: action.payload === "true",
            };
        default:
            return state;
    }
};
